<template>
  <div ref="scrollWarpper" class="scroll-warpper">
    <slot></slot>
  </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
  name: "scroll",
  props: {
    data: {
      type: Array,
      default: [],
    },
  },
  data() {
    return {
      scroll: null,
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.initScroll();

      //  this.scroll.on('pullingUp', () => {
      //   console.log('处理上拉加载操作')
      //   setTimeout(() => {
      //     // 事情做完，需要调用此方法告诉 better-scroll 数据已加载，否则上拉事件只会执行一次
      //     this.scroll.finishPullUp()
      //   }, 2000)
      // })
    });
  },
  watch: {
    data() {
      this.$nextTick(() => {
        this.scroll.refresh();
        //下拉加载完成
        this.scroll.finishPullUp();
      });
    },
  },
  methods: {
    //初始化
    initScroll() {
      if (!this.$refs.scrollWarpper) return;
      this.scroll = new BScroll(this.$refs.scrollWarpper, {
        pullUpLoad: {
          threshold: 100,
          probeType: 3,
          click: true,
          tap: true,
          startY:0
        },
      });
    },
    //数据变更时重新计算
    refresh() {
      this.$refs.scrollWarpper && this.scroll.refresh();
    },
  },
};
</script>

