export function foramtData(arr){
    let newArr =[]
    arr.forEach(item => {
        const detail={}
        detail.id=item.id;
        detail.al=Object.assign({},item.al||item.song.album)
        detail.ar=[].concat(item.ar||item.song.artists)
        detail.name=item.name;
        newArr.push(detail)
    });
    return newArr;
}