import Vue from 'vue'
import Vuex from 'vuex'
import  {getItem,setItem,removeItem} from "@/utils/storage";

Vue.use(Vuex)
const TOKEN_KEY='TOUTIAO_KEY';
export default new Vuex.Store({
  state: {
    user:getItem(TOKEN_KEY)
  },
  mutations: {
    setUser(state,data){
      state.user=data;
     setItem(TOKEN_KEY,data);
    },
    removeUser(state,data){
      state.user=data;
      removeItem(user)
    }
  },
  actions: {
  },
  modules: {
  }
})
