<template>
  <div>
    <top-nav :imgUrl="imgUrl" :name="name"></top-nav>
    <!-- <better-scroll :data="newSongerData" class="betterScroll-warpper"> -->
      <song-list :SongList="newSongerData"> </song-list>
    <!-- </better-scroll> -->
  </div>
</template>

<script>
import { getNewSong } from "@/api/requestHome.js";

import {foramtData} from '../../common/js/utils'

import TopNav from "@/components/TopNav.vue";
import SongList from "@/components/SongList";
// import BetterScroll from "@/components/BetterScroll.vue";

export default {
  components: { TopNav, SongList },
  data() {
    return {
      newSongerData: [],
    };
  },
  computed: {
    imgUrl() {
      if (this.newSongerData.length > 0) {
        return this.newSongerData[0].al.picUrl;
      }
      return "";
    },
    name() {
      if (this.newSongerData.length > 0) {
        return this.newSongerData[0].al.name;
      }
      return "暂无数据";
    },
  },
  async created() {
    const { data } = await getNewSong();
    console.log(data);

    if (data.code === 200) {
      this.newSongerData = foramtData(data.result);
    }
  },
};
</script>

<style lang="less" scoped>
.betterScroll-warpper {
  height: calc(100vh - 500px);
  overflow: hidden;
}
</style>