<template>
  <div>
    <m-header v-if="commentData">评论{{ commentData.total }}</m-header>
    <div class="select-comment">
      <div class="title">评论区</div>
      <div class="select-option">
        <span :class="{ active: 0 === curIndex }" @click="handleClick(0)"
          >推荐</span
        >
        <span :class="{ active: 1 === curIndex }" @click="handleClick(1)"
          >最热</span
        >
      </div>
    </div>
    <better-scroll
      ref="refScroll"
      :data="commentData"
      class="betterScroll-warpper"
    >
      <comment-info
        :commentData="commentData"
        @emitFloorData="hasShowFloor"
      ></comment-info>
    </better-scroll>

    <!-- floor -->
   
      <div class="floor-content" v-show="isFloor" @click="offFloor">
         <transition name="fade" :duration="500">
        <div class="floor-warp" v-show="isFloor" @click.stop="handleStop($event)">
          <m-header :red="false" class="floor-header"
            >{{ floorData.length }} 条回复</m-header
          >
          <comment-info
            :commentData="floorData"
            @emitFloorData="hasShowFloor($event)"
            class="comment-info"
          ></comment-info>
        </div>
            </transition>
      </div>


    <!-- floor - end -->
  </div>
</template>

<script>
import { getMusicComment } from "@/api/requestHome";
import CommentInfo from "./components/CommentInfo.vue";
import mHeader from "@/components/mHeader.vue";
import BetterScroll from "@/components/BetterScroll.vue";

export default {
  name: "music-comment",
  data() {
    return {
      commentData: [],
      recommendCommentData: [],
      hotCommentData: [],
      floorData: [],
      curIndex: 0,
      isFloor: false,
      isFloorWarp:false,
      offset: 6,
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.refScroll.scroll.on("pullingUp", async () => {
        if (this.curIndex === 0) {
          //请求数据
          const data = await getMusicComment({
            offset: this.offset,
            id: this.$route.params.id,
            limit: 10,
          });
          this.offset = this.offset + 11;

          //修改数据截取位置
          //添加数据
          this.recommendCommentData.push(...data.data.comments);
          //   this.commentData.push(...data.data.comments.slice(10));
          return false;
        }
      });
    });
  },
  provide() {
    return {
      id: this.$route.params.id,
    };
  },
  components: {
    CommentInfo,
    mHeader,
    BetterScroll,
  },
  methods: {
    //获取当前歌曲的评论
    async getMusicComment() {
      const { data } = await getMusicComment({
        id: this.$route.params.id,
        limit: 6,
      });
      //初始化数据
      this.commentData = data.comments;
      this.recommendCommentData = data.comments;
      this.hotCommentData = data.hotComments;
    },
    //显示弹出层
    hasShowFloor(floorData) {
      this.isFloor = true;
      this.isFloorWarp=true
      this.floorData = floorData;
    },
    //隐藏弹出层
    offFloor() {
      this.isFloor = false;
        

   
    },
    // 1推荐 和 热门 选择
    handleClick(curindex) {
      this.curIndex = curindex;
      if (curindex == 0) {
        this.commentData = this.recommendCommentData;
        return false;
      }
      this.commentData = this.hotCommentData;
    },
    handleStop(e) {
      e.preventDefault();
    },
  },
  created() {
    this.getMusicComment();
  },
};
</script>

<style lang="less" scoped>
.betterScroll-warpper {
  height: calc(100vh - 140px);
  overflow: hidden;
}

// 过渡动画
.fade-enter-active {
  transition: all .5s ease;
}
.fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.fade-enter, .fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateY(30px);
  opacity: 0;
}


// 过渡动画 -end
.floor-content {
  z-index: 99;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  background: rgba(0, 0, 0, 0.4);
  .floor-warp {
    width: 100vw;
    height: 80vh;
    border-radius: 50px 50px 0 0;
    position: fixed;
    bottom: 0;
    z-index: 99;
    background: whitesmoke;
    overflow: scroll;
    .floor-header {
      position: absolute;
      color: black;
    }
    .comment-info {
      margin-top: 130px;
    }
  }
}

.select-comment {
  display: flex;
  justify-content: space-between;
  margin-top: 120px;
  padding: 0 30px;
  height: 50px;
  box-sizing: border-box;
  .select-option {
    span {
      margin: 0 5px;
      color: #aca1a1;
      &.active {
        color: black;
      }
    }
  }
}
</style>